import React from 'react';
import { H2, GallerySection, GalleryCard} from "./general/style/style";
import { Section } from './general/style/common';
//import { toJS } from 'mobx';
import { DhProperties } from '../DhProperties';

export default function GalleryThreeImage({data}) {

  // console.log("GalleryThreeImage: ",toJS(data));

  const onClickSeeMore = () => {
    if(data.moreImages && data.moreImagesUrl && data.product && data.id){
      if(typeof(newsHuntAction)!=='undefined' && typeof(callEvent)!=="undefined" && ((!isIos() && checkMinAndroidAppVersion(NH_BROWSER_SUPPORTED_VERSION)) || (isIos() && checkMinAndroidAppVersion(NH_BROWSER_SUPPORTED_VERSION_IOS)))){
        const url= DhProperties.properties.pwaBaseUrl+"/webItem?webModel="+encodeURIComponent(JSON.stringify({title:"Gallery",url:DhProperties.properties.baseUrl+data.moreImagesUrl}));
        callEvent(url);
      }else{ 
        window.location.href = DhProperties.properties.baseUrl + data.moreImagesUrl;
       }
    }
}

  return (
    <>
    <GallerySection>
    <div className="fullWidth">
        { data.big_image && <img src={data.big_image} alt="" />}
    </div>
    <div className="halfContainer clearfix">
        <div className="halfWidth">
           {data.small_image1 && <img src={data.small_image1} alt="" />}
        </div>
        <div className="halfWidth" onClick={onClickSeeMore}>
            {data.small_image2 && <img src={data.small_image2} alt="" />}
            {/* {data.moreImages && <span className="view">+{data.moreImageCount}</span>} */}
          {data.moreImages && <a href="javascript:void(0)" className="seeMore"> See More</a>}
        </div>
    </div> 
    {data.name && <p>{data.name}</p>}
  </GallerySection>
  </>
  )
}
